/* Enable scrolling */
body {
  margin: 0;
  overflow-y: auto; /* Enable vertical scrolling */
  padding: 0;
  height: 100%;
}

/* Outer container */
.meme {
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #e3f2fd; /* Light blue background */
  font-family: Arial, sans-serif;
  color: #1c2833; /* Darker navy color for text */
  padding: 20px 0;
}

/* Bio and Title Box */
.bio-title-bubble {
  width: 85%; /* Slightly wider to save space */
  max-width: 1000px;
  padding: 20px;
  background-color: #bbdefb; /* Light blue background */
  border-radius: 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.3);
  margin-bottom: 20px;
}

.bio-title-bubble h1 {
  font-size: 1.8rem;
  margin: 0;
  text-align: center;
  color: #1c2833; /* Darker navy color for titles */
}

.bio-title-bubble p {
  font-size: 0.95rem;
  margin: 10px 0;
  text-align: center;
  color: #1c2833; /* Darker navy color for text */
}

.bio-title-bubble .etsy-link {
  display: inline-block;
  margin-top: 10px;
  padding: 10px 15px;
  background-color: #1e88e5; /* Medium blue background */
  color: white;
  text-decoration: none;
  border-radius: 5px;
}

.bio-title-bubble .etsy-link:hover {
  background-color: #1565c0; /* Darker blue on hover */
}

.bio-title-bubble .bio-text {
  flex: 1;
}

/* Image and Profile Picture */
.meme-photo {
  display: block;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  object-fit: cover;
  object-position: 40% 20%; /* Lower the image to center it better */
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3);
}

/* Carousel container */
.carousel {
  width: 90%;
  max-width: 1100px; /* Smaller by 5% */
  height: auto;
  aspect-ratio: 16 / 9; /* Ensures images maintain a common aspect ratio */
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  margin-bottom: 20px;
  border-radius: 10px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.3);
  background-color: #bbdefb; /* Light blue background */
}

/* Carousel Image */
.slide {
  width: 100%;
  height: 100%;
  background-size: contain; /* Ensures the entire image fits without distortion */
  background-repeat: no-repeat; /* Avoids tiling */
  background-position: center; /* Centers the image */
  transition: opacity 0.5s ease-in-out;
}

/* Navigation buttons */
.nav-button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  font-size: 2rem;
  color: white;
  background: rgba(0, 0, 0, 0.5);
  border: none;
  border-radius: 50%;
  padding: 10px 15px;
  cursor: pointer;
  z-index: 10;
}

.nav-button:hover {
  background: rgba(255, 255, 255, 0.7);
  color: black;
}

.nav-button.prev {
  left: 10px;
}

.nav-button.next {
  right: 10px;
}
